@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#121212;display:inline-flex;align-items:center;justify-content:center;min-width:100vw;min-height:100vh}#App{color:#ff0;font-family:"Abel",sans-serif;font-size:64px;letter-spacing:20px}.fade-in-text{display:inline-block;animation:fadeIn linear 3s;-webkit-animation:fadeIn linear 3s;-moz-animation:fadeIn linear 3s;-o-animation:fadeIn linear 3s;-ms-animation:fadeIn linear 3s}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

.container{display:flex;flex-direction:column;justify-content:space-around;min-height:60vh;min-width:100vw;align-items:center}button{display:block;width:300px;height:100px;background-color:#ff0;border:1px solid black;border-radius:30px;font-size:26px;color:#121212;font-family:"Abel",sans-serif}Link{text-decoration:none}

#mainCaptcha{background:url(/static/media/captcha_bg.2c7533a0.jpeg) no-repeat}

